import { ActionIcon } from '@mantine/core';
import { ArrowLeft } from 'tabler-icons-react';

export default function AppHeader({ selectedApp, onBack }) {
  return (
    selectedApp && (
      <ActionIcon
        className="back-button"
        onClick={onBack}
        variant="outline"
        color="blue"
        size="lg"
      >
        <ArrowLeft size={20} />
      </ActionIcon>
    )
  );
}
